.login-model{width:400px;min-height:260px;background:#fff;border-radius:3px;position:fixed;left:50%;top:50%;margin-left:-200px;margin-top:-200px;z-index:1;box-shadow:0 2px 25px 0 rgba(0,0,0,.3)}.login-model .text-1{padding-top:30px;margin-bottom:-6px}.login-model .text-1 a{width:280px;height:60px;display:block;background:url(/dolphinscheduler/ui/images/login-logo.svg?5afda3b9151225b5d19a6329c0cdcea9) no-repeat 23px;margin:0 auto}.login-model .form-model{padding:30px 20px}.login-model .form-model .list{margin-bottom:24px}.login-model .form-model .list>label{font-size:14px;display:block;height:24px;line-height:24px;font-weight:400;color:#333}.login-model .form-model .list>.error{font-size:12px;color:red;padding-top:6px}.login-model .form-model .list:last-child{margin-bottom:6px}.login-model .ctr{width:400px;text-align:center;position:absolute;left:0;bottom:-80px;color:#fff}